import { useState, useEffect } from 'react';

export const useMCPConnection = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [mcpStatus, setMcpStatus] = useState('disconnected');

  useEffect(() => {
    // Simulate MCP connection check
    const checkConnection = async () => {
      try {
        // In a real implementation, this would check the actual MCP server
        const response = await fetch('/api/mcp/status');
        const data = await response.json();
        
        setIsConnected(data.connected);
        setMcpStatus(data.status);
      } catch (error) {
        console.log('MCP server not running, using mock data');
        // For now, simulate connected state
        setIsConnected(true);
        setMcpStatus('connected');
      }
    };

    checkConnection();
    
    // Poll for connection status every 30 seconds
    const interval = setInterval(checkConnection, 30000);
    
    return () => clearInterval(interval);
  }, []);

  return {
    isConnected,
    mcpStatus
  };
}; 