import React, { useState } from 'react';
import { 
  Search, 
  Filter, 
  Download, 
  Eye, 
  Mail, 
  RefreshCw,
  Plus,
  Star,
  Building,
  Users,
  TrendingUp,
  Globe
} from 'lucide-react';
import toast from 'react-hot-toast';

const ProspectResearch = ({ prospects, loading, onRefresh }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [filterScore, setFilterScore] = useState('all');
  const [sortBy, setSortBy] = useState('score');

  // Mock prospects data
  const mockProspects = [
    {
      id: 1,
      name: 'Bubble.io',
      website: 'bubble.io',
      platformType: 'No-Code Platform',
      qualificationScore: 85,
      employees: '150-300',
      revenue: '$10M-$50M',
      funding: 'Series B',
      status: 'researched',
      lastContacted: '2024-01-15',
      nextAction: 'Generate email'
    },
    {
      id: 2,
      name: 'Airtable',
      website: 'airtable.com',
      platformType: 'Database Platform',
      qualificationScore: 92,
      employees: '500-1000',
      revenue: '$100M-$500M',
      funding: 'Series D',
      status: 'contacted',
      lastContacted: '2024-01-10',
      nextAction: 'Follow up'
    },
    {
      id: 3,
      name: 'Monday.com',
      website: 'monday.com',
      platformType: 'Project Management',
      qualificationScore: 78,
      employees: '1000-5000',
      revenue: '$100M-$500M',
      funding: 'Public',
      status: 'qualified',
      lastContacted: '2024-01-08',
      nextAction: 'Schedule demo'
    },
    {
      id: 4,
      name: 'Notion',
      website: 'notion.so',
      platformType: 'Productivity Platform',
      qualificationScore: 88,
      employees: '500-1000',
      revenue: '$100M-$500M',
      funding: 'Series C',
      status: 'proposal',
      lastContacted: '2024-01-05',
      nextAction: 'Send proposal'
    },
    {
      id: 5,
      name: 'Zapier',
      website: 'zapier.com',
      platformType: 'Automation Platform',
      qualificationScore: 95,
      employees: '500-1000',
      revenue: '$100M-$500M',
      funding: 'Series C',
      status: 'demo_scheduled',
      lastContacted: '2024-01-12',
      nextAction: 'Prepare demo'
    }
  ];

  const filteredProspects = mockProspects.filter(prospect => {
    const matchesSearch = prospect.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         prospect.website.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesScore = filterScore === 'all' || 
                        (filterScore === 'high' && prospect.qualificationScore >= 80) ||
                        (filterScore === 'medium' && prospect.qualificationScore >= 60 && prospect.qualificationScore < 80) ||
                        (filterScore === 'low' && prospect.qualificationScore < 60);
    
    return matchesSearch && matchesScore;
  });

  const sortedProspects = [...filteredProspects].sort((a, b) => {
    switch (sortBy) {
      case 'score':
        return b.qualificationScore - a.qualificationScore;
      case 'name':
        return a.name.localeCompare(b.name);
      case 'revenue':
        return b.revenue.localeCompare(a.revenue);
      default:
        return 0;
    }
  });

  const getScoreColor = (score) => {
    if (score >= 80) return 'text-success-600 bg-success-100';
    if (score >= 60) return 'text-warning-600 bg-warning-100';
    return 'text-danger-600 bg-danger-100';
  };

  const getStatusBadge = (status) => {
    const statusConfig = {
      researched: { label: 'Researched', color: 'status-pending' },
      contacted: { label: 'Contacted', color: 'status-active' },
      qualified: { label: 'Qualified', color: 'status-success' },
      proposal: { label: 'Proposal', color: 'status-warning' },
      demo_scheduled: { label: 'Demo Scheduled', color: 'status-success' }
    };
    
    const config = statusConfig[status] || { label: status, color: 'status-inactive' };
    return <span className={`status-badge ${config.color}`}>{config.label}</span>;
  };

  const handleResearchNew = () => {
    toast.success('Research command sent to MCP server');
  };

  const handleGenerateEmail = (prospect) => {
    toast.success(`Email generation requested for ${prospect.name}`);
  };

  const handleViewDetails = (prospect) => {
    toast.success(`Viewing details for ${prospect.name}`);
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Prospect Research</h1>
          <p className="text-gray-600">Manage and analyze your prospect research data</p>
        </div>
        <div className="flex items-center space-x-3">
          <button
            onClick={onRefresh}
            className="btn-secondary flex items-center space-x-2"
            disabled={loading}
          >
            <RefreshCw className={`w-4 h-4 ${loading ? 'animate-spin' : ''}`} />
            <span>Refresh</span>
          </button>
          <button
            onClick={handleResearchNew}
            className="btn-primary flex items-center space-x-2"
          >
            <Plus className="w-4 h-4" />
            <span>Research New</span>
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="card">
        <div className="flex items-center space-x-4">
          <div className="flex-1">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
              <input
                type="text"
                placeholder="Search prospects..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="input-field pl-10"
              />
            </div>
          </div>
          <select
            value={filterScore}
            onChange={(e) => setFilterScore(e.target.value)}
            className="input-field w-40"
          >
            <option value="all">All Scores</option>
            <option value="high">High (80+)</option>
            <option value="medium">Medium (60-79)</option>
            <option value="low">Low (&lt;60)</option>
          </select>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="input-field w-40"
          >
            <option value="score">Sort by Score</option>
            <option value="name">Sort by Name</option>
            <option value="revenue">Sort by Revenue</option>
          </select>
          <button className="btn-secondary flex items-center space-x-2">
            <Download className="w-4 h-4" />
            <span>Export</span>
          </button>
        </div>
      </div>

      {/* Prospects Table */}
      <div className="card">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-200">
                <th className="text-left py-3 px-4 font-medium text-gray-900">Company</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Platform Type</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Score</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Size</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Revenue</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Status</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Next Action</th>
                <th className="text-left py-3 px-4 font-medium text-gray-900">Actions</th>
              </tr>
            </thead>
            <tbody>
              {sortedProspects.map((prospect) => (
                <tr key={prospect.id} className="border-b border-gray-100 hover:bg-gray-50">
                  <td className="py-4 px-4">
                    <div>
                      <div className="font-medium text-gray-900">{prospect.name}</div>
                      <div className="text-sm text-gray-500 flex items-center space-x-1">
                        <Globe className="w-3 h-3" />
                        <span>{prospect.website}</span>
                      </div>
                    </div>
                  </td>
                  <td className="py-4 px-4">
                    <div className="text-sm text-gray-900">{prospect.platformType}</div>
                  </td>
                  <td className="py-4 px-4">
                    <div className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getScoreColor(prospect.qualificationScore)}`}>
                      <Star className="w-3 h-3 mr-1" />
                      {prospect.qualificationScore}/100
                    </div>
                  </td>
                  <td className="py-4 px-4">
                    <div className="text-sm text-gray-900 flex items-center space-x-1">
                      <Users className="w-3 h-3" />
                      <span>{prospect.employees}</span>
                    </div>
                  </td>
                  <td className="py-4 px-4">
                    <div className="text-sm text-gray-900 flex items-center space-x-1">
                      <TrendingUp className="w-3 h-3" />
                      <span>{prospect.revenue}</span>
                    </div>
                  </td>
                  <td className="py-4 px-4">
                    {getStatusBadge(prospect.status)}
                  </td>
                  <td className="py-4 px-4">
                    <div className="text-sm text-gray-600">{prospect.nextAction}</div>
                  </td>
                  <td className="py-4 px-4">
                    <div className="flex items-center space-x-2">
                      <button
                        onClick={() => handleViewDetails(prospect)}
                        className="p-1 text-gray-400 hover:text-gray-600 transition-colors"
                        title="View Details"
                      >
                        <Eye className="w-4 h-4" />
                      </button>
                      <button
                        onClick={() => handleGenerateEmail(prospect)}
                        className="p-1 text-gray-400 hover:text-gray-600 transition-colors"
                        title="Generate Email"
                      >
                        <Mail className="w-4 h-4" />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Summary Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="card text-center">
          <div className="text-2xl font-bold text-gray-900">{sortedProspects.length}</div>
          <div className="text-sm text-gray-600">Total Prospects</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bold text-success-600">
            {sortedProspects.filter(p => p.qualificationScore >= 80).length}
          </div>
          <div className="text-sm text-gray-600">High Priority</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bold text-warning-600">
            {sortedProspects.filter(p => p.status === 'contacted').length}
          </div>
          <div className="text-sm text-gray-600">Contacted</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bold text-primary-600">
            {Math.round(sortedProspects.reduce((acc, p) => acc + p.qualificationScore, 0) / sortedProspects.length)}
          </div>
          <div className="text-sm text-gray-600">Avg Score</div>
        </div>
      </div>
    </div>
  );
};

export default ProspectResearch; 